$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
}

h1 {
  font-size: 80px;

  @media screen and (max-width: 700px) {
    font-size: 50px;
  }
}

.resources {
  display: flex;
  flex-direction: column;
  padding: 0 12px;
}

.technologies {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  @media screen and (max-width: 1200px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 700px) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.technology {
  padding: 12px 24px;
  border-radius: 14px;
  box-shadow: 1px 3px 7px 1px #EFEFEF;

}

.technology .versions {
  display: flex;
  flex-wrap : wrap;
  gap: 6px;
  color: #636366;
}

.technology .versions .version {
  margin: 0px;
  border: 1px solid #636366;
  font-size: 13px;
  padding: 4px 8px;
  border-radius: 22px;
}

.technology p.title {
  font-weight: 700;
  font-size: 24px;
  margin: 16px 0;
}

.technology p.description {
  margin: 16px 0 12px 0;
}

.technology p.keyword {
  margin: 0 0 32px 0;
  font-size: 15px;
  line-height:22px;
  color: #888;
}

.technology .links {
  margin-top: 32px;
  font-size: 14px;
}

.technology .links p {
  margin: 8px 0;
}

.technology .links p a:link, a:visited {
  color: #000;
}

p.copyright {
  margin-top: 32px;
  font-size: 14px;
}
